@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(to right, #eef2f3, #8e9eab);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

:root {
  --input-color: #ee365b;
  --btn-color: #076585;
  --btn-bg: #61bfdf;
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.input {
  width: 300px;
  padding: 0.5rem;
  margin-top: 5rem;
  font-size: 1.2rem;
  font-family: 'Poppins', sans-serif;
  box-shadow: inset 0 3px 3px 2px rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 15px;
  color: #ee365b;
  color: var(--input-color);
}

.btn {
  padding: 0.5rem;
  margin-top: 5rem;
  margin-left: 2.8rem;
  font-size: 1rem;
  font-family: 'Poppins', sans-serif;
  border: none;
  border-radius: 10px;
  color: #076585;
  color: var(--btn-color);
  background-color: #61bfdf;
  background-color: var(--btn-bg);
  box-shadow: 2px 3px 4px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  transition: all 0.1s;
}

.btn:hover {
  -webkit-filter: brightness(110%);
          filter: brightness(110%);
}

.input,
.delete-btn,
.btn:focus {
  outline: none;
}

.btn:active {
  transform: translateY(4px);
}

.is-completed {
  text-decoration: line-through;
  color: #ee365b;
}

.todo-list {
  min-width: 500px;
  width: 450px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-top: 2rem;
}

.todo {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 0.3rem;
  font-size: 1.2rem;
  cursor: pointer;
}

.delete-btn {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  color: aliceblue;
  background-color: #ee365b;
  background-color: var(--input-color);
  border: none;
  margin-left: 1rem;
  cursor: pointer;
  box-shadow: 2px 3px 4px rgba(0, 0, 0, 0.5);
  transition: all 0.1s;
}

.delete-btn:active {
  transform: translateY(4px);
}

